/* ===================== TESTIMONIAL ======================*/
.gauto-testimonial-area {
    position: relative;
    background: url(../img/bg-theme.svg) no-repeat fixed 0 0 / cover;
    background-color: #000;
    z-index: 1;
    overflow: hidden;
    
}
