/* ===================== HOT OFFERS ======================*/
#offerTab {
    text-align: center;
    width: 100%;
    display: block;
    border: medium none;
    margin: 30px 0;
}

#offerTab li {
    display: inline-block;
    margin: 0 5px;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
    border: medium none;
}

.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:focus {
    border: medium none;
}

.nav-tabs .nav-link {
    border: medium none;
}

#offerTab li.nav-item .nav-link,
#offerTab li.nav-item .nav-link.active {
    display: inline-block;
    text-transform: uppercase;
    padding: 7px 25px;
    font-weight: 500;
    color: #fff;
    font-size: 14px;
    position: relative;
    z-index: 1;
}

#offerTab li.nav-item .nav-link:after {
    position: absolute;
    content: "";
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    background: #020202 none repeat scroll 0 0;
    z-index: -1;
    transform: skew(40deg);
    transition: all 0.3s ease 0s;
}

#offerTab li.nav-item .nav-link:hover:after {
    background: #ec3323 none repeat scroll 0 0;
    transform: skew(-40deg);
}

#offerTab .nav-tabs {
    width: 100%;
    display: block;
    border: medium none;
    margin: 30px 0;
    justify-content: center;
}

#offerTab li.nav-item .nav-link.active:after {
    position: absolute;
    content: "";
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease 0s;
    background: #ec3323 none repeat scroll 0 0;
    transform: skew(-40deg);
}

