}

.offer-action {
    text-align: center;
    background: #020202 none repeat scroll 0 0;
    position: relative;
    z-index: 1;
    overflow: hidden;
    width: 100%;
    margin: 25px auto 0;
    transition: all 0.4s ease 0s;
}

.offer-action a {
    display: inline-block;
    text-transform: uppercase;
    padding: 7px 15px;
    font-weight: 500;
    color: #fff;
    font-size: 14px;
    width: 47%;
}

.offer-action:after {
    position: absolute;
    content: "";
    width: 66%;
    height: 100%;
    background: #ec3323 none repeat scroll 0 0;
    right: -36px;
    z-index: -1;
    transform: skewX(40deg);
    transition: all 0.4s ease 0s;
}

.offer-action:hover {
    background: #ec3323 none repeat scroll 0 0;
}

.offer-action:hover:after {
    background: #020202 none repeat scroll 0 0;
}

.offer-text ul {
    text-align: center;
    margin-top: 10px;
}

.offer-text ul li {
    display: block;
    font-size: 14px;
    margin: 15px;
