    }
}

@media (max-width: 767px) {
    #offerTab li {
        display: block;
        margin-bottom: 5px;
    }

    #offerTab li a {
        display: block !important;
    }

    #offerTab li.nav-item .nav-link.active:after,
    #offerTab li.nav-item .nav-link:after {
        transform: skewX(0);
    }

    #offerTab li.nav-item .nav-link:hover:after {
        transform: skewX(0);
    }

    .single-offers {
        padding: 10px;
    }

 
    #offerTab li.nav-item .nav-link {
        display: block;
        width: 100%;
    }
    .offer-image{
