  vertical-align: middle;
  text-align: center;
  opacity: 0;
  filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  transform-style: preserve-3d;
  transform: perspective(1200px) rotateX(-90deg) translateZ(-45px);
  transition: all 300ms ease 100ms;
}

.title-box h3 {
  font-size: 24px;
