  position: absolute;
  left: 0px;
  bottom: 0px;
  right: 0px;
  text-align: center;
  display: block;
  background: transparent;
  transform-style: preserve-3d;
  transform: perspective(1200px) rotateX(90deg) translateZ(-45px);
  opacity: 0;
  filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  transition: all 300ms ease 200ms;
  z-index: 3;
}

